.price .title_h3 span {
	display: block;
	font-family: $fontFamily;
	font-size: 16px;
	font-weight: 300;
}

.price {
	&__wrap {
		margin-top: 75px;
	}
	&__body {
		display: flex;
		flex-direction: column;
		row-gap: 30px;
		margin-top: 50px;
	}
	&__list {
		display: flex;
		flex-direction: column;
		row-gap: 15px;
	}
	&__item {
		padding: 8px 0px 8px 6px;
		border-radius: 10px;
		background-color: $dark;
	}
	&__img {
		display: none;
	}
	&__config {
	}
}
///////////////////////
.config {
	&__title {
		color: #fff;
		font-family: "Bebas-Neue";
		font-size: 36px;
		font-weight: 700;
	}
	&__table {
		width: 100%;
		border-spacing: 0;
		text-align: center;
	}
	&__time {
		margin-bottom: 10px;
		background-color: $accent;
		font-size: 14px;
		& td {
			padding: 6px 0 14px;
		}
	}
	&__price {
		font-size: 11px;
		& td {
			padding: 17px 0 10px;
		}
	}
}

@media (min-width: 481px) {
	.config {
		&__time {
			font-size: 16px;
		}
		&__price {
			font-size: 14px;
			font-weight: 500;
		}
	}
}

@media (min-width: 641px) {
	.price {
		&__item {
			display: flex;
			column-gap: 20px;
			padding: 20px 0px 32px 20px;
		}
		&__img {
			display: block;
			position: relative;
			flex: 0 0 160px;
			height: 140px;
			border-radius: 10px;
			overflow: hidden;
			& img {
				position: absolute;
				left: 0;
				top: 0;
				width: 100%;
				height: 100%;
				object-fit: cover;
			}
		}
		&__config {
			flex: 1 1 100%;
		}
	}
}

.checklist {
	padding: 28px 41px 46px;
	background-color: $dark;
	border-radius: 10px;
	&__list {
		margin-bottom: 32px;
		color: #fff;
		font-family: "Bebas-Neue";
		font-size: 36px;
		font-weight: 700;
	}
	&__item {
		position: relative;
		padding-left: 43px;
		font-family: $fontFamily;
		font-size: 16px;
		font-weight: 300;
		&:first-child {
			margin-top: 16px;
		}
		&:not(:last-child) {
			margin-bottom: 20px;
		}
		&::before {
			content: "";
			position: absolute;
			left: 0;
			top: -2px;
			width: 28px;
			height: 21px;
			background-image: url("../img/icons/check-mark.svg");
			background-size: contain;
			background-repeat: no-repeat;
		}
	}
	&__btn {
		display: block;
		margin: 0 auto;
		padding: 27px 70px;
		color: #fff;
		font-size: 16px;
		font-style: normal;
		font-weight: 600;
		border-radius: 10px;
		background: linear-gradient(rgb(0, 87, 255) 0px, rgb(33, 130, 244) 100%);
		transition: all 0.3s ease-in;
		&:hover {
			background: linear-gradient(rgb(33, 130, 244) 100%, rgb(0, 87, 255) 0px);
		}
	}
}

@media (min-width: 1025px) {
	.price{
		&__body{
			flex-direction: row;
			column-gap: 20px;
		}
		&__list{
			flex: 1 0 500px;
		}
		&__checklist{
			flex: 0 1 380px;
			align-self: flex-start;
		}
	}
}
